import { Component, OnInit } from '@angular/core';
import { Name } from './interface';

@Component({
  selector: 'app-tab1',
  templateUrl: 'tab1.page.html',
  styleUrls: ['tab1.page.scss']
})


export class Tab1Page implements OnInit {
  public squares: any;
  public squaresCount: number = 300;

  constructor() {}

  public ngOnInit() : void {
    let sqCount: Name[] = [];
    for (let i = 0; i <= this.squaresCount; i++) {
      const sample: boolean = !Math.round(Math.random());
      sqCount[i] = {id: i, active: sample};
    }

    this.squares = {
      "name":"Comp 1",
      "list":sqCount
    };

  }

    public onItemChange(sq, $event) : void {
      $event.currentTarget.nextElementSibling.classList.remove("box-small");
      $event.currentTarget.nextElementSibling.classList.add("my-square");
      console.log($event.currentTarget.nextElementSibling);
      if (sq.active) {
        alert("Too late - Square already taken");
      } else {
        sq.active = true;
      }

    }

}
